import { Injectable } from '@angular/core';
import { HttpClient, HttpHandler, HttpHeaders } from '@angular/common/http';

import { appConfig } from '../server/app.config';
// config
import { clsTrabajador } from '../models/clsTrabajador';

@Injectable({
  providedIn: 'root'
})
export class TrabajadorService {
  private url_trabajdor = appConfig.apiUrl + 'api/trabajador';

  constructor(private http: HttpClient) {
    console.log('servicio activo para trabajadores');
  }


  /* -------------------- GET DE CONFIGURACION ------------------ */
  getTrabajador() {
    return this.http.get<clsTrabajador[]>(this.url_trabajdor);
  }

  post_trabajador(datos) {    
    const headers = new HttpHeaders().set('Content-Type', 'application/json');    
    return this.http.post(this.url_trabajdor, datos, { headers: headers });    
  }

  delete_trabajador(id) {
    return this.http.delete(this.url_trabajdor + '/' + id);
  }
  



}// end class
